<template>
<div>
    <Loading v-if="MusicList.length<=0"></Loading>
    <ul class="newMusicList">
        <ListItem v-for="item in MusicList" :key="item.id" :item="item" ></ListItem>
    </ul>
</div>
</template>

<script>
import Loading from './Loading'
import ListItem from './ListItem'
export default {
    name:"MusicItem",
    props:{
        MusicList:Array
    },
    components:{
        Loading,
        ListItem
    },
    watch:{
        MusicList(v){
            //清空
            this.$root.playingMusic.playList = [];
            for(let value of v){
                this.$root.playingMusic.playList.push(value.id);
            }
        }
    },
}
</script>
