<template>
    <li :key="item.id"  @click="playMusic(item.id)">
        <div class="right" v-if="num>0">{{num|formatNumber}}</div>
        <div class="left">
            <div>{{item.name}}</div>
            <p><i :class="item.song.privilege.maxbr > 320000?'icon':''"></i>{{item.song.artists[0].name}}-{{item.name}}</p>
        </div>
        <span class="play"></span>
    </li>
</template>

<script>
export default {
    props:{
        item:Object,
        num:{
            type:Number,
            default:-1
        }
    },
    filters:{
        formatNumber(value){
            return value < 10?'0'+value:value;
        }
    },
    methods:{
        playMusic(id){
            this.$root.playingMusic.musicID = id;
            this.$root.playingMusic.isPlay = true;
        }
    }
}
</script>

<style lang="less" scoped>
li {
    display: flex;
    position: relative;
    margin-top: 10px;
    padding-left:10px;
    align-items: center;
    div.left {
        flex: 1;
        text-align: left;
        border-bottom: 1px solid #ddd;
        padding: 6px 0px;
        div {
            font-size: 17px;
            color: black;
        }
        p {
            font-size: 12px;
            color: #888;
            .icon {
                display: inline-block;
                width: 12px;
                height: 8px;
                margin-right: 4px;
                background: url(../assets/play.png) no-repeat;
                background-size: 166px 97px;
            }
        }
    }
    .play {
        position: absolute;
        width: 22px;
        height: 22px;
        right: 10px;   
        top: 10px; 
        background: url('../assets/play.png') no-repeat -24px 0px;
        background-size: 166px 97px;
    }
    div.right {
        position: relative;
        left: -5px;
        color: #999999; 
    }
}
li:nth-of-type(1) {
    .right {
        color: #E13436;
    }
}
li:nth-of-type(2) {
    .right {
        color: #E13436;
    }
}
li:nth-of-type(3) {
    .right {
        color: #E13436;
    }
}
</style>