<template>
    <div class="recommend">
        <TopNav v-show="!this.$root.playingMusic.isFullscreenShow"></TopNav>
        <Title>推荐歌单</Title>
        <Loading v-if="recommendMusicList.length<=0"></Loading>
        <ul class="recommendList" @click="enterFullscreen">
            <router-link v-for="rem in recommendMusicList" :key="rem.id" :to="{path:`/musiclist/${rem.id}`}" tag="li">
                <div>    
                    <img :src="rem.picUrl">
                    <span>{{rem.playCount|formatNum}}</span>
                </div>
                <p>{{rem.name|substr(26)}}</p>
            </router-link>
        </ul>  
        <Title>最新音乐</Title>
        <MusicItem :MusicList="newMusicList"></MusicItem>
    </div>
</template>
<script>
import TopNav from '../components/TopNav'
import Title from '../components/Title'
import MusicItem from '../components/MusicItem'
import Loading from '../components/Loading'
export default {
    name:'Recommend',
    components:{
        Title,
        MusicItem,
        Loading,
        TopNav
    },
    data(){
        return {
            recommendMusicList:[],
            newMusicList:[]
        }
    },
    beforeRouteEnter(to,from,next){//路由守卫 ，路由进入前获取数据
        next(vm=>{
            vm.$http.get("/personalized?limit=6").then(data=>{
                vm.recommendMusicList = data.data.result;
            }).catch(()=>{
                console.log("请求推荐歌单资源失败");
            });
            vm.$http.get('/personalized/newsong').then(data=>{
                vm.newMusicList = data.data.result;
            }).catch(()=>{
                console.log("请求最新音乐资源失败");
            });
        });
    },
    filters:{
        formatNum(value){
            return (value/10000).toFixed(1)+"万"; //保留一位小数
        },
        substr(value,length){
            return value.substr(0,length) + '···';
        }
    },
    methods:{
        enterFullscreen(){
            this.$root.playingMusic.enterSongSheet = true;
        }
    }
}
</script>
<style lang="less" scoped>
ul.recommendList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
        width: 33%;
        margin-bottom: 15px;
        div {
            position: relative;
            span {
                top:2px;
                right: 3px;
                position: absolute;
                color: white;
                text-shadow: 1px 1px 3px rgba(0, 0, 0, .5);
            }
        }
        p {
            font-size: 13px;
            text-align: left;
            padding: 6px 2px 0px 6px ;
            word-wrap: break-word;
        }
    }
}
</style>