<template>
<div class="play">
    <div class="player" >
        <audio ref='musicplayer' autoplay :src="`https://music.163.com/song/media/outer/url?id=${musicID}.mp3`" @timeupdate="timeUpdate"></audio>
        <div class="playerBar" v-show="musicID!=0 && !this.$root.playingMusic.isFullscreenShow">
            <i ref="rollImg">
                <img :src="song.al.picUrl">
            </i>
            <i @click="enterFullLrc">{{song.name}}-{{song.ar[0].name}}</i>
            <i @click="togglePlay">
                <canvas ref="canvas" width="50" height='50' :class="$root.playingMusic.isPlay?'play':'stop'"></canvas>
            </i>
        </div>
    </div>
    <transition name="full-enter">
    <div class="fullscreen" v-show="this.$root.playingMusic.isFullscreenShow" >
        <span class="back" @click="back"></span>
        <div class="bg" :style="{backgroundImage:`url(${song.al.picUrl})`}"></div>
        <div class="fullLrc" >
            <h3 class="title">{{song.name}}-{{song.ar[0].name}}</h3>
            <div class="content">
                <div class="lrc" :style="lrcCurrent>5?{
                    transform:`translateY(-${(lrcCurrent-4)*35}px)`}:{}">
                    <ul>
                        <li v-for="(item,index) in lrcArr" :key="index" :class="{'active':index==lrcCurrent}">{{item.lyric}}</li>
                    </ul>
                </div>
            </div>
            <div class="progress" @click="changeProgress">
                <span class="current">{{current|formatTime}}</span>
                <span class="duration">{{duration|formatTime}}</span>
                <div class="all"></div>
                <div class="section" :style="{width:`${progress}%`}"></div>
            </div>
            <div class="controler">
                <span class="prev" @click="prev"></span>
                <span class="play-stop"><i :class="$root.playingMusic.isPlay?'play':'stop'" @click="togglePlay"></i></span>
                <span class="next" @click="next"></span>
            </div>
        </div>
    </div>
    </transition>
</div>
</template>
<script>
export default {
    props:["musicID"],
    data(){
        return {
            song:{
                al:{
                    picUrl:''
                },
                ar:[
                    {
                        name:""
                    }
                ],
                name:""
            },
            lrcArr:[], //歌词信息
            lrcCurrent:0, //歌词下标
            progress:0,
            current:0,
            duration:0
        }
    },
    watch:{
        musicID(value){
            //初始化信息
            this.song={
                al:{
                    picUrl:''
                },
                ar:[
                    {
                        name:""
                    }
                ],
                name:""};
            this.lrcArr=[];
            //请求歌曲信息
            this.duration = this.$refs.musicplayer.duration;
            if(isNaN(this.duration)){
                this.duration = 0;
            }
            this.$http.get('/song/detail?ids='+value).then(data=>{
                this.song = data.data.songs[0];
            }).catch(()=>{
                console.log("请求歌曲信息失败");
            });
            //临时修改服务器地址
            this.$http.defaults.baseURL="http://music.kele8.cn"; 
            this.$http.get('/lyric?id='+value).then(data=>{
                let lrc = data.data.lrc.lyric;  
                let lrcArr = lrc.split(/\n/);
                let reg = /\[(\d+):(\d+\.\d+\])(.+)/;
                this.lrcArr = lrcArr.map(lr=>{
                    reg.test(lr);
                    return {
                        //转化为秒
                        time:parseInt(RegExp.$1)*60+parseFloat(RegExp.$2),
                        lyric:RegExp.$3
                    }
                });
            }).catch(()=>{
                console.log("获取歌词失败");
            });
            //修正服务器地址
            this.$http.defaults.baseURL="http://musicapi.leanapp.cn/";
            return value;
        }
    },
    methods:{
        togglePlay(){
            if(this.$root.playingMusic.isPlay){
                this.$refs.musicplayer.pause();
                this.$root.playingMusic.isPlay = false;
                this.$refs.rollImg.style.animationPlayState = "paused";
            }else {
                this.$refs.musicplayer.play();
                this.$root.playingMusic.isPlay = true;
                this.$refs.rollImg.style.animationPlayState = "running";
            }
        },
        timeUpdate(event){
            let audio = event.target;
            let currentTime = audio.currentTime;
            this.current = currentTime;
            this.duration = audio.duration;
            if(isNaN(this.duration)){
                this.duration = 0;
            }
            //歌词显示
            for(let i in this.lrcArr){  
                //歌词的时间
                let lrcTime = this.lrcArr[i].time;
                //歌词是否显示
                //是否是最后一条
                if(i == this.lrcArr.length-1){
                    break;
                }
                if(currentTime>=lrcTime && currentTime<this.lrcArr[Number(i)+1].time){
                    this.lrcCurrent = i;
                    break;
                }
            }
            this.drawCircle(currentTime,this.duration);
            //判断是否播放完毕
            if(this.$refs.musicplayer.duration == this.$refs.musicplayer.currentTime){
                this.$refs.rollImg.style.animationPlayState = "paused";
            }
            this.progress = currentTime/this.duration*100;
        },
        drawCircle(currentTime,duration){
            let canvas = this.$refs.canvas;
            if(canvas=='undefined'){
                console.log("获取canvas失败");
                return ;
            }
            let ctx = canvas.getContext("2d");
            ctx.beginPath();
            ctx.strokeStyle = 'RGB(230,231,233)';
            ctx.arc(25,25,10,0,Math.PI*2);
            ctx.stroke();
            ctx.closePath();

            ctx.beginPath();
            ctx.strokeStyle = "black";
            ctx.arc(25,25,10,0,Math.PI*2*currentTime/duration);
            ctx.stroke();
            ctx.closePath();
        },
        enterFullLrc(){
            this.$root.playingMusic.isFullscreenShow = true;
        },
        outFullLrc(){
            this.$root.playingMusic.isFullscreenShow = false;
        },
        prev(){
            let id = this.$root.playingMusic.musicID;
            let index = this.$root.playingMusic.playList.indexOf(id);
            index--;
            if(index<0){
                index = this.$root.playingMusic.playList.length-1;
            }
            this.$root.playingMusic.musicID = this.$root.playingMusic.playList[index];
            this.$root.playingMusic.isPlay = true;
        },
        next(){
            let id = this.$root.playingMusic.musicID;
            let index = this.$root.playingMusic.playList.indexOf(id);
            index++;
            if(index >= this.$root.playingMusic.playList.length ){
                index = 0;
            }
            this.$root.playingMusic.musicID = this.$root.playingMusic.playList[index];
            this.$root.playingMusic.isPlay = true;
        },
        back(){
            this.$root.playingMusic.isFullscreenShow = false;
        },
        changeProgress(event){
            this.progress = event.offsetX/parseInt(getComputedStyle(event.target,null)['width'])*100;
            this.$refs.musicplayer.currentTime = this.progress/100*this.$refs.musicplayer.duration;
        },

    },
    filters:{
        formatTime(value){
            let seconds = parseInt(value % 60);
            let minutes = parseInt(value / 60);
            if(seconds < 10){
                seconds = '0' + seconds;
            }
            if(minutes < 10){
                minutes = '0' + minutes;
            }
            return minutes + ":" + seconds;
        }
    }
}
</script>

<style lang="less" scoped>
.play {
    .player {
        position: fixed;
        bottom: 0px;
        width: 100%;
        text-align: center;
        z-index: 1;
        .playerBar {
            display: flex;
            width: 100%;
            height: 50px;
            background-color: RGB(30,210,168);
            align-items: center;
            i {
                height: 50px;
                display: inline-block;
                &:first-of-type {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    overflow: hidden;
                    margin-left: 10px;
                    animation: rollImg  3s infinite linear;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                &:last-of-type {
                    width: 50px;
                    canvas {
                        background-size: 100% 100%;
                    }
                    canvas.stop {
                        background: url(../assets/play-fill.svg) no-repeat center center;
                    }
                    canvas.play {
                        background: url(../assets/pause-fill.svg) no-repeat center center;
                    }
                }
                &:nth-of-type(2) {
                    flex: 1;
                    line-height: 50px;
                    font-style: normal;
                    overflow: hidden;
                }
            }
        }
    }
    .fullscreen{
        position: fixed;
        width: 100vw;
        height: 100vh;
        top: 0px;
        left: 0px;
        overflow: hidden;
        background-color: RGB(59,59,70);
        .back {
            position: absolute;
            width: 30px;
            height: 30px;
            background: url(../assets/chevron-double-left.svg) no-repeat center;
            // background: url(../assets/caret-left.svg) no-repeat center;
            border-radius: 50%;

            top: 10px;
            left: 10px;
            z-index: 3;
            overflow: hidden;
            background-color: RGB(30,210,168);
        }
        .bg {
            position: absolute;
            z-index: 1;
            top: 0px;
            bottom: 0px;
            left: 0px;
            right: 0px;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            &::after {
                content: "";
                position: absolute;
                top: 0px;
                bottom: 0px;
                left: 0px;
                right: 0px;
                background-color: rgba(0,0,0,.6);
            }
        }
        .fullLrc {
            position: absolute;
            z-index: 2;
            top: 0px;
            left: 0px;
            bottom: 0px;
            right: 0px;
            .title {
                text-align: center;
                color: yellow;
                margin: 60px 0px 20px 0px;
            }
            .content {
                text-align: center;
                height: 60vh;
                line-height: 35px;
                overflow: hidden;
                color: RGB(146,157,161);
                .lrc {
                    transition:transform 1s linear;
                    ul {
                        li.active {
                            color: yellow;
                        }
                    }
                }
            }  
            .progress {
                height: 8px;
                width: 70%;
                margin:50px auto 0px ;
                position: relative;
                .all {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    border-radius: 20px;
                    background-color: grey;
                    top: 0px;
                    left: 0px;
                }
                .section {
                    position: absolute;
                    height: 100%;
                    width: 0;
                    border-radius: 20px;
                    background-color: green;
                    top: 0px;
                    left: 0px;
                }
                .current {
                    position:absolute;
                    top: -7px;
                    left: -50px;
                    color: white;
                }
                .duration {
                    position:absolute;
                    top: -7px;
                    right: -50px;
                    color: white;
                }
            }
            .controler {
                display: flex;
                position: absolute;
                width: 100%;
                bottom: 20px;
                left: 0px;
                justify-content: center;
                span {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    margin: 0px 10px;
                    background-color: RGB(30,210,168);
                    position: relative;
                    overflow: hidden;
                }
                .play-stop {
                    i {
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        transform: scale(2);
                    }
                    i.play {
                        background: url(../assets/pause-fill.svg) no-repeat center;
                    }
                    i.stop {
                        background: url(../assets/play-fill.svg) no-repeat center;
                    }
                }
                .prev::after {
                    content: "";
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    width: 100%;
                    height: 100%;
                    transform: scale(1.5);
                    background: url(../assets/caret-left-fill.svg) no-repeat center;
                }
                .next::after {
                    content: "";
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    width: 50px;
                    height: 50px;
                    transform: scale(1.5);
                    background: url(../assets/caret-right-fill.svg) no-repeat center;
                }

            }
        }
    }
    .full-enter-enter,.full-enter-leave-to {
        transform: translateX(-100%);
    }
    .full-enter-to,.full-enter-leave  {
        transform: translateX(0);
    }
    .full-enter-enter-active,.full-enter-leave-active {
        transition: all .3s ease;
    }
}
@keyframes rollImg {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
</style>
